.app-form-box {
	position: relative;
	transition: box-shadow 0.3s ease;
	cursor: pointer;

	&::after {
		content: '';
		position: absolute;
		display: block;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		border-radius: 10px;
		border: 2px solid #fff;
		z-index: -1;
		opacity: 0;
		transition: opacity 0.3s ease;
	}

	&.active {
		box-shadow: 0px 0px 45px #3445a5;
		&::after {
			opacity: 1;
		}
	}
	&__heading {
		font-size: 24px;
		font-weight: bold;
		line-height: $baseline;
		margin-bottom: $baseline * 2;
	}

	.price {
		font-size: 36px;
		font-weight: bold;
		line-height: $baseline * 1;
		color: $c-secondary;
		margin-bottom: $baseline;
	}
}

.app-form-section {
	position: relative;

	a {
		&:hover,
		&:focus {
			color: $c-primary-light;
		}
	}

	&__bg {
		display: block;
		position: absolute;
		top: 0;
		left: 0;

		background-color: #222058;
		width: 100%;
		height: 100%;
		//display: none;
		z-index: -1;
		user-select: none;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.input-wrap {
	position: relative;
	//background-color: seagreen;
	height: $baseline * 2;
	font-size: 18px;
	color: #fff;
	margin-right: 35px;
	margin-left: 35px;
	margin-bottom: $baseline;

	&::before {
		display: block;
		position: absolute;
		content: '';
		height: 3px;
		width: 100%;
		border-radius: 1.5px;
		background-color: $c-primary-dark;
		bottom: 0;
		left: 0;
	}

	.focus-line {
		display: block;
		position: absolute;
		bottom: 0;
		left: 0;
		height: 3px;
		width: 100%;
		border-radius: 1.5px;
		background-color: $c-primary-light;
		transform: scaleX(0);
		transform-origin: left;
		transition: transform 0.3s ease;
	}

	input {
		border: 0;
		background-color: transparent !important;
		height: 100%;
		width: 100%;
		padding-left: 15px;
		color: #fff;
		&.has-content + label {
			transform: translateY(-110%);
			font-size: 14px;
			color: $c-primary-light;
		}

		&.has-content ~ .focus-line {
			transform: scaleX(1);
		}

		&:focus {
			outline: 0;
		}

		&:-webkit-autofill {
			transition-delay: 99999s;
		}
	}

	label {
		position: absolute;
		top: 50%;
		left: 0;
		transform: translateY(-50%);
		line-height: $baseline * 2;
		height: 100%;
		transition: transform 0.3s ease;
		transition-property: transform, font-size, color;
		padding-left: 15px;
		padding-right: 15px;
		font-weight: bold;
		cursor: text;
		pointer-events: none;
	}

	input:focus + label {
		transform: translateY(-110%);
		font-size: 14px;
		color: $c-primary-light;
	}
	input:focus ~ .focus-line {
		transform: scaleX(1);
	}
}

.textarea-wrap {
	height: 100%;
	width: 100%;
	border: solid 3px $c-primary-dark;
	transition: border 0.3s ease;
	border-radius: 10px;
	&.has-content {
		border: solid 3px $c-primary-light;
	}

	textarea {
		resize: none;
		width: 100%;
		height: 100%;
		padding-top: 15px;
		padding-left: 15px;
		color: #fff;
		border: 0;
		background-color: transparent !important;

		&::placeholder {
			color: #fff;
			font-size: 18px;
			font-weight: bold;
		}

		&:focus {
			outline: 0;
		}
	}
}

// phone 768px
@media (max-width: map-get($grid-breakpoints, md) - 0.063em) {
	.input-wrap {
		margin-left: 0;
		margin-right: 0;
	}

	.textarea-wrap {
		height: $baseline * 4;
	}
}
