/* purgecss start ignore */

$selectric-main-color: #ddd !default; // Color used in border
$selectric-secondary-color: #bbb !default; // Color used in button
$selectric-text-color: $c-orange !default; // Color used in label
$selectric-bg-color: #f8f8f8 !default; // Background color
$selectric-btn-bg-color: #f8f8f8 !default; // Button background color
$selectric-height: 48px !default; // Outer height
$selectric-spacing: 10px !default; // Label left padding
$selectric-border-width: 0px !default; // Outer border width
$selectric-border-radius: 0px !default; // Border radius
$selectric-inner-height: $selectric-height - ($selectric-border-width * 2) !default; // Inner height
$selectric-font-size: 18px !default; // Font size

.selectric-wrapper {
	position: relative;
	cursor: pointer;
	margin-right: 35px;
	margin-left: 35px;

	&::before {
		display: block;
		position: absolute;
		bottom: 0;
		left: 0;
		content: '';
		height: 3px;
		width: 100%;
		border-radius: 1.5px;
		background-color: $c-primary-dark;
	}
}

.selectric-responsive {
	width: 100%;
}

.selectric {
	border: $selectric-border-width solid $selectric-main-color;
	border-radius: $selectric-border-radius;
	background: transparent;
	position: relative;
	overflow: hidden;

	.label {
		display: block;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		margin: 0 $selectric-inner-height 0 $selectric-spacing;
		font-size: $selectric-font-size;
		font-weight: bold;
		line-height: $selectric-inner-height;
		color: $selectric-text-color;
		height: $selectric-inner-height;
		user-select: none;
	}

	.button {
		display: block;
		position: absolute;
		right: 0;
		top: 0;
		width: $selectric-inner-height;
		height: $selectric-inner-height;
		text-align: center;

		&:after {
			content: '';
			position: absolute;
			background-image: url('images/ico/arrow_botom.svg');
			background-repeat: no-repeat;
			background-size: 19px;
			background-position: 50%;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			transition: transform 0.3s ease;
		}
	}
}

.selectric-focus .selectric {
	.button::after {
		transform: translateY(5px);
	}
}

.selectric-open {
	z-index: 9999;

	.selectric-items {
		//display: block;
		opacity: 1;
	}
}

.selectric-disabled {
	filter: alpha(opacity=50);
	opacity: 0.5;
	cursor: default;
	user-select: none;
}

.selectric-hide-select {
	position: relative;
	overflow: hidden;
	width: 0;
	height: 0;

	select {
		position: absolute;
		left: -100%;
	}

	&.selectric-is-native {
		position: absolute;
		width: 100%;
		height: 100%;
		z-index: 10;

		select {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			height: 100%;
			width: 100%;
			border: none;
			z-index: 1;
			box-sizing: border-box;
			opacity: 0;
		}
	}
}

.selectric-input {
	position: absolute !important;
	top: 0 !important;
	left: 0 !important;
	overflow: hidden !important;
	clip: rect(0, 0, 0, 0) !important;
	margin: 0 !important;
	padding: 0 !important;
	width: 1px !important;
	height: 1px !important;
	outline: none !important;
	border: none !important;
	*font: 0/0 a !important;
	background: none !important;
}

.selectric-temp-show {
	position: absolute !important;
	visibility: hidden !important;
	display: block !important;
}

/* Items box */
.selectric-items {
	display: none;
	display: block;
	opacity: 0;
	transition: opacity 0.3s ease;
	position: absolute;
	top: 100%;
	left: 0;
	background-color: $c-primary-dark;

	border: 0px solid $c-primary-dark;
	z-index: -1;
	box-shadow: 0 0 10px -6px;

	.selectric-scroll {
		height: 100%;
		overflow: auto;
	}

	.selectric-above & {
		top: auto;
		bottom: 100%;
	}

	ul,
	li {
		list-style: none;
		padding: 0;
		margin: 0;
		font-size: $selectric-font-size;
		line-height: 20px;
		min-height: 20px;
	}

	li {
		display: block;
		padding: 10px;
		color: #fff;
		cursor: pointer;
		transition: background-color 0.3s ease;

		&.selected {
			background: $c-secondary;
			color: $c-white;
		}

		&.highlighted {
			background: $c-secondary;
			color: $c-white;
		}

		&:hover {
			background: $c-secondary;
			color: $c-white;
		}
	}

	.disabled {
		filter: alpha(opacity=50);
		opacity: 0.5;
		cursor: default !important;
		background: none !important;
		color: #666 !important;
		user-select: none;
	}

	.selectric-group {
		.selectric-group-label {
			font-weight: bold;
			padding-left: 10px;
			cursor: default;
			user-select: none;
			background: none;
			color: #444;
		}

		&.disabled li {
			filter: alpha(opacity=100);
			opacity: 1;
		}

		li {
			padding-left: 25px;
		}
	}
}

// phone 768px
@media (max-width: map-get($grid-breakpoints, md) - 0.063em) {
	.selectric-wrapper {
		margin-left: 0;
		margin-right: 0;
	}
}

/* purgecss end ignore */
