.intro-banner {
	position: relative;
	color: #fff;
	text-transform: uppercase;
	font-weight: 700;
	//background-color: #ddd;
	display: flex;
	justify-content: center;
	padding-top: $baseline * 7;
	padding-bottom: $baseline * 7;
	text-align: right;
	//overflow-x: hidden;
	//overflow-y: visible;

	> header {
		display: inline-flex;
		flex-direction: column;
		transform: translateX(-150px);
		user-select: none;
	}
	h1 {
		font-weight: 700;
		letter-spacing: -2px;
		font-size: $baseline * 2;
		line-height: $baseline * 3;
	}

	.wpp {
		font-weight: 700;
		letter-spacing: -2px;
		font-size: $baseline * 2.5;
		font-size: $baseline * 3;
		line-height: $baseline * 2;
		margin-bottom: $baseline;
	}

	.aca {
		font-size: $baseline * 2.95;
		font-size: $baseline * 3.5;
		line-height: $baseline * 3;
		margin-bottom: $baseline;
	}

	header > span {
		font-size: 26px;
		//background-color: #eee;
		text-align: right;
		color: $c-orange;
	}

	&__img {
		position: absolute;
		left: 50%;
		transform: translateX(-21%);
		top: -40px;
		width: 550px;
		height: auto;
		user-select: none;
		max-width: 100%;
	}
}

//to 991px
@media (max-width: map-get($grid-breakpoints, lg) - 0.063em) {
	.intro-banner {
		.wpp {
			font-size: $baseline * 2.5;
		}

		.aca {
			font-size: $baseline * 2.95;
		}
		&__img {
			width: 490px;
			top: -20px;
		}
	}
}

//to 768px
@media (max-width: map-get($grid-breakpoints, md) - 0.063em) {
	.intro-banner {
		justify-content: flex-start;
		margin-left: 15px;
		overflow: hidden;

		padding-top: $baseline * 5;
		padding-bottom: $baseline * 5;
		> header {
			transform: translateX(0);
		}

		&__img {
			width: 365px;
			top: 0px;
			transform: translateX(-15%);
		}
	}
}

//to 576px
@media (max-width: map-get($grid-breakpoints, sm) - 0.063em) {
	.intro-banner {
		justify-content: center;
		margin-left: 15px;
		margin-right: 15px;

		padding-top: 0;
		padding-bottom: $baseline * 2;
		&__img {
			display: none;
		}
		.wpp,
		.aca {
			line-height: $baseline * 2;
			font-size: $baseline * 2;
		}

		.wpp {
			margin-bottom: 0;
		}

		> header {
			text-align: center;
			> span {
				text-align: center;
			}
		}

		header > span {
			font-size: 16px;
		}
	}
}
