.selectric-wrapper {
  position: relative;
  cursor: pointer;
  margin-right: 35px;
  margin-left: 35px;
}

.selectric-wrapper::before {
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  content: '';
  height: 3px;
  width: 100%;
  border-radius: 1.5px;
  background-color: #1d1e57;
}

.selectric-responsive {
  width: 100%;
}

.selectric {
  border: 0px solid #ddd;
  border-radius: 0px;
  background: transparent;
  position: relative;
  overflow: hidden;
}

.selectric .label {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0 48px 0 10px;
  font-size: 18px;
  font-weight: bold;
  line-height: 48px;
  color: #ffb5a1;
  height: 48px;
  user-select: none;
}

.selectric .button {
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  width: 48px;
  height: 48px;
  text-align: center;
}

.selectric .button:after {
  content: '';
  position: absolute;
  background-image: url("images/ico/arrow_botom.svg");
  background-repeat: no-repeat;
  background-size: 19px;
  background-position: 50%;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transition: transform 0.3s ease;
}

.selectric-focus .selectric .button::after {
  transform: translateY(5px);
}

.selectric-open {
  z-index: 9999;
}

.selectric-open .selectric-items {
  opacity: 1;
}

.selectric-disabled {
  filter: alpha(opacity=50);
  opacity: 0.5;
  cursor: default;
  user-select: none;
}

.selectric-hide-select {
  position: relative;
  overflow: hidden;
  width: 0;
  height: 0;
}

.selectric-hide-select select {
  position: absolute;
  left: -100%;
}

.selectric-hide-select.selectric-is-native {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.selectric-hide-select.selectric-is-native select {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  border: none;
  z-index: 1;
  box-sizing: border-box;
  opacity: 0;
}

.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  *font: 0/0 a !important;
  background: none !important;
}

.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important;
}

/* Items box */
.selectric-items {
  display: none;
  display: block;
  opacity: 0;
  transition: opacity 0.3s ease;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #1d1e57;
  border: 0px solid #1d1e57;
  z-index: -1;
  box-shadow: 0 0 10px -6px;
}

.selectric-items .selectric-scroll {
  height: 100%;
  overflow: auto;
}

.selectric-above .selectric-items {
  top: auto;
  bottom: 100%;
}

.selectric-items ul,
.selectric-items li {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 18px;
  line-height: 20px;
  min-height: 20px;
}

.selectric-items li {
  display: block;
  padding: 10px;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.selectric-items li.selected {
  background: #5d91fd;
  color: #fff;
}

.selectric-items li.highlighted {
  background: #5d91fd;
  color: #fff;
}

.selectric-items li:hover {
  background: #5d91fd;
  color: #fff;
}

.selectric-items .disabled {
  filter: alpha(opacity=50);
  opacity: 0.5;
  cursor: default !important;
  background: none !important;
  color: #666 !important;
  user-select: none;
}

.selectric-items .selectric-group .selectric-group-label {
  font-weight: bold;
  padding-left: 10px;
  cursor: default;
  user-select: none;
  background: none;
  color: #444;
}

.selectric-items .selectric-group.disabled li {
  filter: alpha(opacity=100);
  opacity: 1;
}

.selectric-items .selectric-group li {
  padding-left: 25px;
}

@media (max-width: 47.937em) {
  .selectric-wrapper {
    margin-left: 0;
    margin-right: 0;
  }
}

