.main-nav a {
  color: #a0b7ff;
  display: block;
  transition: color 0.3s ease;
  line-height: 3rem;
  padding: 0 5px;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  font-weight: 700;
}

.main-nav a::before {
  position: absolute;
  right: 51%;
  bottom: 0;
  left: 51%;
  content: '';
  z-index: -1;
  background: #5d91fd;
  border-radius: 2px;
  height: 4px;
  transition-property: left, right;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.main-nav a:hover::before, .main-nav a:focus::before, .main-nav a:active::before {
  right: 15%;
  left: 15%;
}

.main-nav a:hover, .main-nav a:focus {
  text-decoration: none;
  color: #fff;
}

.wp-logo {
  margin-right: 15px;
  transition: margin-right 0.3s ease;
}

.wp-logo img,
.wp-logo svg {
  width: 3rem;
  height: 3rem;
}

.close-button {
  display: none;
}

.ham-button {
  display: none;
}

.btn-application-wrap .btn {
  font-size: 15px;
  text-transform: uppercase;
  transition: box-shadow 300ms cubic-bezier(0.2, 0, 0.7, 1), transform 200ms cubic-bezier(0.2, 0, 0.7, 1);
}

.btn-application-wrap .btn:hover, .btn-application-wrap .btn:focus {
  background-color: #5d91fd;
  box-shadow: 0 0 1px 5px rgba(93, 145, 253, 0.4), 0 0 1px 15px rgba(93, 145, 253, 0.1), 0 0 1px 25px rgba(93, 145, 253, 0.1);
}

@media (min-width: 87.5em) {
  .main-header {
    padding-left: 70px;
    padding-right: 70px;
  }
  .main-nav li {
    margin-right: 35px;
  }
  .main-nav li:last-of-type {
    margin-right: 5px;
  }
  .wp-logo {
    margin-right: 35px;
  }
}

@media (min-width: 62em) and (max-width: 74.937em) {
  .main-header .btn {
    padding: 0 15px;
  }
  .main-nav li {
    margin-right: 5px;
  }
}

@media (max-width: 61.937em) {
  .main-nav {
    order: 6;
    position: fixed;
    right: 0;
    top: 0;
    width: 50vw;
    height: 100vh;
    z-index: 110;
    background-color: #fff;
    transform: translateX(105%);
    will-change: transform;
    box-shadow: -10px 0px 5px 0px rgba(0, 0, 0, 0.1);
  }
  .main-nav.js-open {
    transform: translateX(0);
  }
  .main-nav.js-transtion {
    transition: transform 0.4s ease;
  }
  .main-nav__list {
    flex-direction: column;
  }
  .main-nav li {
    margin-right: 0;
    padding-left: 15px;
    padding-right: 15px;
  }
  .main-nav li.current-item a::before, .main-nav li.current-item a::before, .main-nav li.current-item a::before {
    left: 0;
    right: 80%;
  }
  .main-nav a::before {
    left: 0;
    right: 100%;
  }
  .main-nav a:hover:before, .main-nav a:focus:before, .main-nav a:active:before {
    left: 0;
    right: 90%;
  }
  .main-nav a:hover, .main-nav a:focus {
    color: #1d1e57;
  }
  .close-button {
    display: block;
  }
  .ham-button {
    display: block;
    order: 1;
    margin-right: auto;
  }
  .btn-application-wrap {
    order: 2;
  }
  .closest-term {
    order: 2;
  }
  .wp-logo {
    display: none;
  }
}

@media (max-width: 47.937em) {
  .main-nav {
    width: 75vw;
  }
}

@media (max-width: 35.937em) {
  .main-nav {
    width: 85vw;
  }
  .headroom {
    transition: transform 0.3s ease;
  }
  .headroom--pinned {
    transform: translateY(0);
  }
  .headroom--top {
    transform: translateY(-3rem);
  }
  .headroom--unpinned {
    transform: translateY(-6rem);
  }
  .closest-term {
    display: none;
  }
}

.bg-header {
  background-color: #222058;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 75rem;
}

.bg-header img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: 50% 0;
}

.boxes-reasons {
  margin-bottom: 4.5rem;
  position: relative;
}

.boxes-reasons::after {
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  content: "";
  height: 1px;
  width: calc(100% - 130px);
  background-color: rgba(87, 110, 188, 0.21);
  transform: translateX(-50%);
}

.boxes-reasons::before {
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  content: "";
  height: 4.5rem;
  width: 1px;
  transform: translate(-50%, 100%);
  background-color: rgba(87, 110, 188, 0.21);
}

.base-heading {
  font-size: 30px;
  font-weight: bold;
  font-style: normal;
  text-transform: uppercase;
  color: #292b62;
  margin-bottom: 3rem;
  text-align: center;
  width: auto;
}

.lecturers-section {
  background-color: #f5f5fa;
}

.bg-semitran {
  background-color: rgba(87, 110, 188, 0.21);
  border-radius: 10px;
}
