.box-reason {
	margin-bottom: $baseline;
	text-align: center;

	&::before {
		display: block;
		position: absolute;
		bottom: 0;
		left: 50%;
		content: '';
		height: 24px;
		width: 1px;
		background-color: rgba(#576ebc, 0.21);
		transform: translate(-50%, 100%);
	}
	&__inner {
		padding-top: $baseline * 2;
		padding-bottom: $baseline * 2;
		background-color: rgba(#576ebc, 0.21);

		border-radius: 8px;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		padding-left: 15px;
		padding-right: 15px;
		//height: 250px;
	}

	&__heading {
		color: #fff;
		font-size: 18px;
		line-height: $baseline;
		text-transform: uppercase;
		margin-bottom: 0;
	}

	&__fancy-bar {
		display: block;
		position: absolute;
		top: 16px;
		left: 50%;
		height: 8px;
		width: 80px;
		background-color: #1d1e57;
		transform: translateX(-50%);
		border-radius: 3px;
	}

	&::after {
		content: '';
		position: absolute;
		display: block;
		bottom: -10px;
		left: 50%;
		transform: translateX(-50%);
		background-color: $c-orange;
		width: 20px;
		height: 20px;
		border-radius: 100%;
	}
}

//992px 1200px
@media (min-width: map-get($grid-breakpoints, lg)) and (max-width: map-get($grid-breakpoints, xl) - 0.063em) {
	.box-reason {
		&__heading {
			font-size: 16px;
		}
	}
}
