.faq-item {}

.faq-item-inner {
	margin-bottom: $baseline;
	padding-left: 40px;
	margin-left: 20px;
	padding-right: 10px;
	position: relative;
	cursor: pointer;
	font-size: 18px;
	font-weight: bold;
	color: $c-secondary;

	&.active {
		.faq-item-copy {
			//display: block;
			pointer-events: all;
			opacity: 1;
		}

		&::before {
			transform: rotate(0deg);
		}

		&::after {
			background-color: #fff;
			z-index: 5;
		}
	}

	&::before {
		content: '';
		position: absolute;
		display: block;
		left: -20px;
		top: -5px;
		width: 40px;
		height: 40px;
		background-color: $c-white;
		border-radius: 100%;
		background-image: url('images/ico/minus.svg');
		background-repeat: no-repeat;
		background-position: 50%;
		transform: rotate(90deg);
		transition: transform 0.3s ease;
		box-shadow: 0px 5px 20px 0 rgba(35, 33, 89, 0.1);
	}

	&::after {
		content: '';
		position: absolute;
		display: block;
		left: -20px;
		top: -5px;
		width: 40px;
		height: 40px;
		//background-color: blue;
		background-image: url('images/ico/minus.svg');
		background-repeat: no-repeat;
		background-position: 50%;
		transform: rotate(0deg);
		transition: background-color 0.3s ease;
		border-radius: 100%;

		z-index: 1;
	}
}

.faq-item-label {
	font-size: $baseline;
	font-weight: bold;
	margin-top: 0;
	margin-bottom: 0;
	font-size: 18px;
	margin-top: 5px;
	line-height: $baseline;
}

.faq-item-text {
	font-size: 16px;
	font-weight: normal;
	color: $c-gray;
	z-index: 5;
	top: 0;
	right: 0;
	width: 100%;
	margin-top: 8px;
	padding-right: 40px;

	transition: trasform 0.3s ease;
}

.faq-item-copy {
	position: absolute;
	left: -5px;
	top: -5px;
	background-color: #fff;
	padding-top: 0px;
	padding-left: 45px;
	padding-right: 5px;

	padding-bottom: 10px;
	border-radius: 7px;
	border-top-left-radius: 0;
	box-shadow: 0px 5px 20px 0 rgba(35, 33, 89, 0.1);
	transition: opacity 0.3s ease;
	width: 100%;

	opacity: 0;
	pointer-events: none;
	z-index: 2;

}