.closest-term {
	display: flex;
	font-weight: 700;
	&__icon {
		margin-right: 10px;
		transform: translateY(-1px);
		user-select: none;
		img {
			width: $baseline;
			height: $baseline;
		}
	}

	&__text {
		font-size: 15px;
		text-transform: uppercase;
		color: $c-primary-light;
		margin-right: 15px;
	}

	&__date {
		color: $c-orange;
		font-size: 24px;
		margin-right: 20px;
	}
}

//992px 1200px
@media (min-width: map-get($grid-breakpoints, lg)) and (max-width: map-get($grid-breakpoints, xl) - 0.063em) {
	.closest-term {
		&__text {
			font-size: 14px;
		}
		&__date {
			font-size: 16px;
		}
	}
}

//max 992px
@media (max-width: map-get($grid-breakpoints, lg) - 0.063em) {
	.closest-term {
		&__text {
			font-size: 14px;
			//display: none;
		}
		&__date {
			font-size: 16px;
		}
	}
}

//max 768px
@media (max-width: map-get($grid-breakpoints, md) - 0.063em) {
	.closest-term {
		&__text {
			display: none;
		}
	}
}
