.when-we-start-section {
	//margin-bottom: $baseline * 3;
	&__inner {
		position: relative;
		border-radius: 10px;
		padding-top: $baseline * 2;
		padding-bottom: $baseline * 2;
		box-shadow: $s-01;

		&::after,
		&::before {
			display: block;
			position: absolute;
			top: 0px;
			left: 50%;
			//border-radius: 10px;
			border-top-right-radius: 10px;
			border-top-left-radius: 10px;
			content: '';
			transform: translate(-50%, -100%);
		}

		&::after {
			height: 10px;
			width: calc(100% - 30px);
			background-color: #d2d2dc;
		}
		&::before {
			height: 20px;
			width: calc(100% - 60px);
			background-color: #aeadc1;
		}

		.row {
			position: relative;
			&::after {
				content: '';
				position: absolute;
				display: block;
				width: 1px;
				height: 100%;
				background-image: linear-gradient(rgb(0, 0, 0) 1px, transparent 5px);
				background-image: linear-gradient(
					#cfcfda 0px,
					#cfcfda 5px,
					rgba(0, 0, 0, 0) 5px
				);
				background-size: auto 10px;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
	}

	&__heading {
		color: $c-primary;
		line-height: $baseline;
		font-weight: bold;
		font-size: 28px;
		text-transform: uppercase;
	}

	&__col {
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
	}

	.icon {
		// background-color: rgba(255, 0, 0, 0.418);
		display: flex;
		width: 72px;
		height: 72px;
		justify-content: center;
		margin-bottom: $baseline;
		user-select: none;
		img {
			max-width: 100%;
			height: auto;
		}
	}

	.adress {
		color: $c-primary;
		font-size: 20px;
		font-weight: bold;
		margin-bottom: $baseline * 2;
		> span {
			display: block;
			color: $c-gray;
			font-size: 14px;
			font-weight: normal;
		}
	}

	.subheading {
		font-size: 24px;
		font-weight: bold;
		line-height: $baseline;
		color: $c-primary;
		margin-bottom: $baseline * 2;
	}

	.date {
		font-size: 36px;
		font-weight: bold;
		line-height: $baseline * 2;
		color: $c-secondary;
		margin-bottom: $baseline * 2;
	}

	.link-wrap {
		text-decoration: underline;
		line-height: $baseline * 2;
	}

	.btn-container {
		max-width: 325px;
		width: 100%;
		margin-bottom: 0;
	}

	.btn {
		width: 100%;
	}
}

// phone
@media (max-width: map-get($grid-breakpoints, md) - 0.063em) {
	.when-we-start-section {
		&__inner {
			padding-bottom: $baseline * 1;
			.row {
				&::after {
					display: none;
				}
			}
		}

		&__col {
			margin-bottom: $baseline * 2;
			&:last-of-type {
				margin-bottom: 0;
			}
		}

		&__heading {
			margin-bottom: $baseline * 3;
		}

		.icon {
			display: none;
		}
	}
}
