html {
	&.baseline {
		position: relative;
		//pointer-events: none;
		&::before {
			position: absolute;
			top: -1px;
			left: 0;
			content: "";
			width: 100%;
			height: 100%;
			background-image: linear-gradient(rgb(11, 226, 215) 1px, transparent 1px);
			background-size: auto 24px;
			z-index: 998;
		}
	}
}

.baseline-toggle {
	display: inline-block;
	position: fixed;
	right: 0;
	bottom: 0;
	line-height: $baseline * 2;
	font-size: r(15);
	font-weight: 700;
	padding: 0 13px;
	border: 2px solid red;
	cursor: pointer;
	background: $c-white;
	user-select: none;
	z-index: 999;
}

body {
	-webkit-font-smoothing: antialiased;
	position: relative;
}

// * override container max-width globaly
.container {
	max-width: 1400px !important;
}

//Mask
.js-mask {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: $c-black;
	z-index: 100;
	opacity: 0.5;
}

// .js-no-scroll {
// 	height: 100vh;
// 	overflow: hidden;
// }

a {
	color: $c-secondary;
	transition: color 0.3s ease;
	&:hover,
	&:focus {
		color: $c-primary;
	}
}

img {
	display: block;
	-ms-interpolation-mode: bicubic;
	opacity: 1;
	transition: opacity 0.3s ease;
	height: auto;

	&[data-src]:not(.loaded) {
		opacity: 0;
	}

	&[data-src],
	&[data-srcset] {
		display: block;
		min-height: 1px;
	}
}

.img-holder {
	position: relative;

	&::after {
		content: "";
		display: block;
	}

	img {
		max-height: 100%;
		max-width: 100%;
	}

	&.img-center {
		img {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}

	&.img-placeholder {
		background: rgba(204, 204, 204, 0.22);
	}
}

//To fight Mozilla's default styling
*::placeholder {
	opacity: 1;
}

// BG lazyload

.bg-loaded {
	position: relative;

	* {
		z-index: 2;
	}

	.bg-block {
		z-index: 1 !important;
	}
}

.bg-block {
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0;
	top: 0;
	transition: opacity 0.3s ease;
	opacity: 0;

	&[style] {
		opacity: 1;
	}
}

//Embed iframe
iframe {
	margin-bottom: 1rem;
	max-width: 100%;
}

.embed-responsive {
	margin-bottom: 1rem;
}

@media (max-width: map-get($grid-breakpoints, sm) - 0.063em) {
	.js-no-scroll {
		//height: 100vh;
		overflow: hidden;
	}
}
