.faq-section {
	background-color: #f5f5fa;
}

.faq-list {
	margin-bottom: $baseline;
	line-height: $baseline;

	li {
		margin-bottom: $baseline;
		padding-left: 40px;
		margin-left: 20px;
		padding-right: 10px;
		position: relative;
		cursor: pointer;
		font-size: 18px;
		font-weight: bold;
		color: $c-secondary;

		//background-color: #fff;

		&.active {
			.faq-list__copy {
				//display: block;
				pointer-events: all;
				opacity: 1;
			}

			&::before {
				transform: rotate(0deg);
			}

			&::after {
				background-color: #fff;
				z-index: 5;
			}
		}

		&::before {
			content: '';
			position: absolute;
			display: block;
			left: -20px;
			top: -5px;
			width: 40px;
			height: 40px;
			background-color: $c-white;
			border-radius: 100%;
			background-image: url('images/ico/minus.svg');
			background-repeat: no-repeat;
			background-position: 50%;
			transform: rotate(90deg);
			transition: transform 0.3s ease;
			box-shadow: 0px 5px 20px 0 rgba(35, 33, 89, 0.1);
		}

		&::after {
			content: '';
			position: absolute;
			display: block;
			left: -20px;
			top: -5px;
			width: 40px;
			height: 40px;
			//background-color: blue;
			background-image: url('images/ico/minus.svg');
			background-repeat: no-repeat;
			background-position: 50%;
			transform: rotate(0deg);
			transition: background-color 0.3s ease;
			border-radius: 100%;

			z-index: 1;
		}
	}

	&__text {
		font-size: 16px;
		font-weight: normal;
		color: $c-gray;
		z-index: 5;
		//position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		margin-top: 8px;
		padding-right: 40px;

		transition: trasform 0.3s ease;
	}

	&__copy {
		position: absolute;
		left: -5px;
		top: -5px;
		background-color: #fff;
		padding-top: 5px;
		padding-left: 45px;
		padding-right: 5px;

		padding-bottom: 10px;
		border-radius: 7px;
		border-top-left-radius: 0;
		box-shadow: 0px 5px 20px 0 rgba(35, 33, 89, 0.1);
		transition: opacity 0.3s ease;
		width: 100%;

		//display: none;
		opacity: 0;
		pointer-events: none;
		z-index: 2;
	}
}

//* to 768px
@media (max-width: map-get($grid-breakpoints, md) - 0.063em) {
	.faq-section {
		&__container {
			padding-left: 30px;
			padding-right: 30px;
		}
	}
}