body {
	padding-top: $baseline * 5; //fixed header offset
}

.main-header {
	display: flex;
	align-items: center;
	position: absolute;
	width: 100%;
	top: $baseline * 2;
	transition: background-color 0.3s ease;
	transition-property: background-color, padding-left, padding-right;
	z-index: 90;
	padding-left: 15px;
	padding-right: 15px;

	&.js-fixed {
		position: fixed;
		top: 0;
		padding-bottom: 10px;
		padding-top: 10px;
		background-color: rgba($c-primary, 0.99);
		box-shadow: $s-light;
	}
}

.main-nav {
	flex-grow: 1;
	font-size: 15px;

	&__list {
		display: flex;
	}

	li {
		margin-right: 15px;
		transition: margin-right 0.3s ease;
		&.current-item {
			a {
				&::before,
				&::before,
				&::before {
					right: 25%;
					left: 25%;
				}
			}
		}
	}

	a {
		color: $c-primary-light;
		display: block;
		transition: color 0.3s ease;
		line-height: $baseline * 2;
		padding: 0 5px;
		transform: perspective(1px) translateZ(0);
		box-shadow: 0 0 1px rgba(0, 0, 0, 0);
		position: relative;
		overflow: hidden;
		text-transform: uppercase;
		font-weight: 700;

		&::before {
			position: absolute;
			right: 51%;
			bottom: 0;
			left: 51%;
			content: '';
			z-index: -1;
			background: $c-secondary;
			border-radius: 2px;
			height: 4px;
			transition-property: left, right;
			transition-duration: 0.3s;
			transition-timing-function: ease-out;
		}

		&:hover::before,
		&:focus::before,
		&:active::before {
			right: 15%;
			left: 15%;
		}

		&:hover,
		&:focus {
			text-decoration: none;
			color: #fff;
		}
	}
}

.wp-logo {
	margin-right: 15px;
	transition: margin-right 0.3s ease;
	img,
	svg {
		width: $baseline * 2;
		height: $baseline * 2;
	}
}

.close-button {
	display: none;
}

.ham-button {
	display: none;
}

.btn-application-wrap {
	.btn {
		font-size: 15px;
		text-transform: uppercase;
		//box-shadow: 0px 5px 15px #3445a5, inset 0px 1px 0px #69a6ff;
		transition: box-shadow 300ms cubic-bezier(0.2, 0, 0.7, 1),
			transform 200ms cubic-bezier(0.2, 0, 0.7, 1);
		&:hover,
		&:focus {
			background-color: $c-secondary;
			box-shadow: 0 0 1px 5px rgba($c-secondary, 0.4),
				0 0 1px 15px rgba($c-secondary, 0.1),
				0 0 1px 25px rgba($c-secondary, 0.1);
		}
	}
}

//min 1400px
@media (min-width: 87.5em) {
	.main-header {
		//background-color: red;
		padding-left: 70px;
		padding-right: 70px;
	}

	.main-nav {
		li {
			margin-right: 35px;
			&:last-of-type {
				margin-right: 5px;
			}
		}
	}

	.wp-logo {
		margin-right: 35px;
	}
}

//min 1200px
@media (min-width: map-get($grid-breakpoints, xl)) {
}

//992px 1200px
@media (min-width: map-get($grid-breakpoints, lg)) and (max-width: map-get($grid-breakpoints, xl) - 0.063em) {
	.main-header {
		.btn {
			padding: 0 15px;
		}
	}

	.main-nav {
		li {
			margin-right: 5px;
		}
	}
}

//max 992px
@media (max-width: map-get($grid-breakpoints, lg) - 0.063em) {
	.main-nav {
		order: 6;
		position: fixed;
		right: 0;
		top: 0;
		width: 50vw;
		height: 100vh;
		z-index: 110;
		background-color: #fff;
		transform: translateX(105%);
		will-change: transform;
		box-shadow: -10px 0px 5px 0px rgba(0, 0, 0, 0.1);

		&.js-open {
			transform: translateX(0);
		}

		&.js-transtion {
			transition: transform 0.4s ease;
		}

		&__list {
			flex-direction: column;
		}

		li {
			margin-right: 0;
			padding-left: 15px;
			padding-right: 15px;
			&.current-item {
				a {
					&::before,
					&::before,
					&::before {
						left: 0;
						right: 80%;
					}
				}
			}
		}

		a {
			&::before {
				left: 0;
				right: 100%;
			}

			&:hover:before,
			&:focus:before,
			&:active:before {
				left: 0;
				right: 90%;
			}

			&:hover,
			&:focus {
				color: $c-primary-dark;
			}
		}
	}

	.close-button {
		display: block;
	}

	.ham-button {
		display: block;
		order: 1;
		//flex-grow: 1;
		margin-right: auto;
	}

	.btn-application-wrap {
		//flex-grow: 1;
		order: 2;
	}

	.closest-term {
		order: 2;
	}

	.wp-logo {
		display: none;
	}
}

//768px
@media (max-width: map-get($grid-breakpoints, md) - 0.063em) {
	.main-nav {
		width: 75vw;
	}
}

//576px
@media (max-width: map-get($grid-breakpoints, sm) - 0.063em) {
	.main-nav {
		width: 85vw;
	}

	.headroom {
		transition: transform 0.3s ease;
		//will-change: transform;
	}

	.headroom--pinned {
		transform: translateY(0);
	}

	.headroom--top {
		transform: translateY($baseline * -2);
	}

	.headroom--unpinned {
		transform: translateY($baseline * -4);
	}

	.closest-term {
		display: none;
	}
}
