/* purgecss start ignore */
.btn {
	user-select: none;
	display: inline-block;
	border: none;
	vertical-align: top;
	cursor: pointer;
	position: relative;

	&:focus {
		outline: 0;
	}

	&--primary {
		background-color: $c-secondary;
		line-height: $baseline * 2;
		border-radius: $baseline;
		padding: 0 45px;
		font-weight: 700;
		color: #fff;
		text-shadow: 0px 1px 2px $c-text-shadow;
		transition: background-color 0.3s ease;

		background-color: #5d91fd;
		transition: background-color 0.3s ease;
		//box-shadow: 0px 5px 15px #3445a5, inset 0px 1px 0px #69a6ff;
		letter-spacing: 0.5px;
		background-image: linear-gradient(to right, #5d83fd, #5ca4fd);
		position: relative;
		transition: box-shadow 300ms cubic-bezier(0.2, 0, 0.7, 1),
			transform 200ms cubic-bezier(0.2, 0, 0.7, 1);
		&::after {
			content: '';
			position: absolute;
			display: block;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			border-radius: $baseline;
			box-shadow: 0px 5px 15px #3445a5, inset 0px 1px 0px #69a6ff;
		}

		&:hover,
		&:focus {
			text-decoration: none;
			color: #fff;
			background-color: $c-secondary;
			box-shadow: 0 0 1px 5px rgba($c-secondary, 0.4),
				0 0 1px 15px rgba($c-secondary, 0.1),
				0 0 1px 25px rgba($c-secondary, 0.1);
		}
	}

	&--gradient {
		background-image: linear-gradient(to right, #5d83fd, #5ca4fd);
		line-height: $baseline * 2;
		border-radius: $baseline;
		font-weight: 700;
		color: #fff;
		text-shadow: 0px 1px 2px $c-text-shadow;
		padding: 0 45px;
		text-transform: uppercase;
		letter-spacing: 0.9px;
		transition: box-shadow 300ms cubic-bezier(0.2, 0, 0.7, 1),
			transform 200ms cubic-bezier(0.2, 0, 0.7, 1);

		&::after {
			display: block;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			content: '';
			border-radius: $baseline;
			box-shadow: inset 0px 1px 0px #69a6ff;
		}

		&:hover,
		&:focus {
			background-color: $c-secondary;
			box-shadow: 0 0 1px 5px rgba($c-secondary, 0.4),
				0 0 1px 15px rgba($c-secondary, 0.1),
				0 0 1px 25px rgba($c-secondary, 0.1);
		}
	}
	&--big {
		line-height: $baseline * 1;
		padding-top: $baseline;
		padding-bottom: $baseline;
		border-radius: 36px;
		font-size: 17px;
		text-transform: uppercase;
		&::after {
			border-radius: 36px;
		}
	}
}

.ham-button {
	display: inline-block;
	position: relative;
	width: $baseline * 2;
	height: $baseline * 3; // background-color: seagreen;
	cursor: pointer;
	text-align: center;
	text-transform: uppercase;
	font-size: 12px;

	&:hover {
		.ham-button__text {
			color: $c-primary-light;
		}
		path {
			fill: $c-primary-light;
		}
	}

	&__text {
		display: block;
		color: $c-white;
		transform: translateY(3px);
		transition: color 0.3s ease;
	}

	svg {
		position: absolute;
		bottom: 10px;
		left: 50%;
		transform: translateX(-50%);
		width: $baseline * 1.75;
		height: $baseline * 1.75;
	}

	path {
		transition: fill 0.3s ease;
		fill: $c-white;
	}
}

.close-button {
	width: $baseline * 2;
	height: $baseline * 2;
	margin-top: $baseline;
	margin-left: auto;
	margin-right: $baseline;
	cursor: pointer;
	color: $c-primary;

	&:hover {
		path {
			fill: $c-primary-light;
		}
	}

	svg {
		width: $baseline * 2;
		height: $baseline * 2;
	}

	path {
		transition: fill 0.3s ease;
		fill: $c-primary;
	}
}

// Loading
.btn-loading {
	cursor: default;
	position: relative;
	&::before {
		content: '';
		background-image: repeating-linear-gradient(
			-45deg,
			$c-primary,
			$c-primary 11px,
			$c-primary-dark 10px,
			$c-primary-dark 20px
		);
		background-size: 28px 28px;
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		animation: move 0.5s linear infinite;
		z-index: -1;
	}
}
@keyframes move {
	0% {
		background-position: 0 0;
	}
	100% {
		background-position: 28px 0;
	}
}
// phone portrait
@media (max-width: map-get($grid-breakpoints, sm) - 0.063em) {
	.btn {
		&--big {
			line-height: $baseline * 2;
			padding-top: 0;
			padding-bottom: 0;
			font-size: 13px;
			padding: 0 5px;
			width: 100%;
		}
	}
}
/* purgecss end ignore */
