.tag-capacity {
	position: absolute;
	top: -8px;
	background-color: $c-secondary;
	padding: 16px;
	border-radius: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: 15px;
	font-weight: bold;

	&--left {
		left: -8px;
		border-bottom-right-radius: 80px;
		padding: 16px 35px 35px 16px;
	}

	&--right {
		right: -8px;
		border-bottom-left-radius: 80px;
		padding: 16px 16px 35px 35px;
	}

	&__icon {
		width: 24px;
		height: 24px;
		margin-bottom: 8px;
		user-select: none;
		img {
			width: 100%;
			height: auto;
		}
	}
}

//To 576px

@media (max-width: map-get($grid-breakpoints, sm) - 0.063em) {
	.tag-capacity {
		padding: 10px;
		border-radius: 10px;
		flex-direction: row-reverse;
		//top: -25px;

		&--left {
			left: 0px;
		}

		&--right {
			right: 0;
		}

		&__icon {
			margin-bottom: 0px;
			margin-left: 5px;
		}
	}
}
