.program-list {
	list-style: none;
	counter-reset: my-awesome-counter;

	li {
		counter-increment: my-awesome-counter;
		margin-bottom: $baseline * 2;
		padding-left: 50px;
		color: $c-gray;
		position: relative;
		&::before {
			content: counter(my-awesome-counter);
			position: absolute;
			top: 0;
			left: 0px;
			color: $c-primary;
			font-weight: 700;
			background-color: $c-orange;
			width: 36px;
			height: 36px;
			text-align: center;
			border-radius: 100%;
			vertical-align: middle;
			line-height: 36px;
			font-size: 23px;
			font-weight: bold;
		}
	}
}

.program-list-block {
	&__heading {
		font-size: 28px;
		font-weight: bold;
		color: $c-primary;
		line-height: $baseline * 1;
		margin-bottom: $baseline * 2;
	}
}

//* to 992px
@media (max-width: map-get($grid-breakpoints, xl) - 0.063em) {
	.program-list-section {
		padding-left: 45px;
		padding-right: 45px;
	}
}

//* to 768px
@media (max-width: map-get($grid-breakpoints, md) - 0.063em) {
	.program-list-section {
		.base-heading {
			text-align: left;
		}
	}
}
