.bg-header {
	background-color: #222058;
	position: absolute;
	left: 0;
	top: 0;
	z-index: -1;
	width: 100%;
	height: $baseline * 50;

	img {
		//max-width: 100%;
		//width: 100%;
		//height: auto;
		height: 100%;
		width: 100%;

		object-fit: cover;
		object-position: 50% 0;
	}
}

.boxes-reasons {
	margin-bottom: $baseline * 3;

	position: relative;
	//background-color: red;
	&::after {
		display: block;
		position: absolute;
		bottom: 0;
		left: 50%;
		content: "";
		height: 1px;
		width: calc(100% - 130px);
		background-color: rgba(#576ebc, 0.21);
		transform: translateX(-50%);
	}

	&::before {
		display: block;
		position: absolute;
		bottom: 0;
		left: 50%;
		content: "";
		height: $baseline * 3;
		width: 1px;
		transform: translate(-50%, 100%);
		background-color: rgba(#576ebc, 0.21);
	}
}

.base-heading {
	font-size: 30px;
	font-weight: bold;
	font-style: normal;
	text-transform: uppercase;
	color: $c-primary;
	margin-bottom: $baseline * 2;
	text-align: center;
	width: auto;
}

.lecturers-section {
	background-color: #f5f5fa;
}

.bg-semitran {
	background-color: rgba(#576ebc, 0.21);
	border-radius: 10px;
}
