body {
	line-height: $baseline;
	font-family: $f-primary;
	font-weight: 400;
	-webkit-font-smoothing: antialiased;
}

html {
	font-size: 16px;
}

#{$headings} {
	font-family: $f-primary;
	font-weight: 400;
	margin-bottom: 0;
	max-width: 80ch;
	font-size: $baseline;
	line-height: $baseline;
	margin-bottom: $baseline;
}

h1 {
	font-size: $baseline * 2.5;
	line-height: $baseline * 3;
}

h2 {
	font-size: $baseline * 2;
	line-height: $baseline * 2;
}

h3 {
	font-size: $baseline * 1.5;
	line-height: $baseline * 2;
}

h4 {
	font-size: $baseline * 1.25;
	line-height: $baseline * 2;
}

p {
	font-size: 16px;
	line-height: $baseline;
	max-width: 80ch;
	margin-bottom: $baseline;
}

div {
	line-height: $baseline;
}

ul,
ol {
	list-style: none;
	padding: 0;
	margin: 0;
}

strong,
b {
	font-weight: 700;
}
