.lecturer-block {
	text-align: center;
	margin-bottom: $baseline * 2;
	&__inner {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__img {
		height: $baseline * 8;
		width: $baseline * 8;
		margin-bottom: $baseline;

		border-radius: 100%;
		position: relative;
		z-index: 2;
		&::before {
			content: '';
			position: absolute;
			//display: block;
			top: -5px;
			left: -5px;
			right: -5px;
			bottom: -5px;
			background: $c-white;
			z-index: 1;
			border-radius: 100%;
			box-shadow: 0px 5px 20px 0 rgba(35, 33, 89, 0.15);
		}
		img {
			position: relative;
			z-index: 3;
			width: 100%;
			height: 100%;
			border-radius: 100%;

			object-fit: cover;
		}
	}

	&__name {
		font-size: 20px;
		font-weight: bold;
		line-height: $baseline;
		color: $c-primary;
		margin-bottom: 0;
	}

	&__web {
		text-decoration: underline;
		margin-bottom: $baseline;
	}

	&__about {
		color: $c-gray;
		padding-left: 20px;
		padding-right: 20px;
		margin-bottom: $baseline;
	}
}

// phone portrait
@media (max-width: map-get($grid-breakpoints, sm) - 0.063em) {
	.lecturer-block {
		&__about {
			display: none;
		}

		&__img {
			height: $baseline * 5;
			width: $baseline * 5;
			&--same-size {
				height: $baseline * 8;
				width: $baseline * 8;
			}
		}

		&__name {
			font-size: 14px;
			margin-bottom: 0;
		}

		&__web {
			font-size: 12px;
		}
	}
}
